@use "../variables.scss" as v;

.load-more {
  padding: 25px 0;

  @media (min-width: v.$tablet-width) {
    display: flex;
    flex-direction: column;
    padding-left: 38px;
    padding-right: 37px;

    border: 2px solid v.$secondary-color;
  }

  @media (min-width: v.$desktop-width) {
    padding: 38px;
  }
}

.load-more__title {
  display: flex;
  justify-content: center;
  margin-bottom: 8px;

  font-family: "Oswald", system-ui;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;

  text-transform: uppercase;
  color: #111111;

  @media (min-width: v.$tablet-width) {
    margin: 0 54px;
    margin-top: 136px;
    margin-bottom: 23px;

    font-size: 20px;
    line-height: 22px;
    text-align: center;
  }

  @media (min-width: v.$desktop-width) {
    margin-left: 0;
    margin-right: 0;
    margin-top: 160px;
  }
}

.load-more__description {
  display: flex;
  justify-content: center;
  margin: 0;
  margin-bottom: 21px;

  font-family: "Arial", system-ui;
  font-size: 12px;
  line-height: 16px;
  color: v.$special-text;

  @media (min-width: v.$tablet-width) {
    margin: 0 40px;
    text-align: center;
  }

  @media (min-width: v.$desktop-width) {
    margin: 0;
  }
}

.load-more__button {
  display: block;
  width: 100%;
  min-height: 40px;

  border: 0;
  cursor: pointer;

  font-family: "Oswald", system-ui;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: v.$special-text;
  background-color: #f2f2f2;

  @media (min-width: v.$tablet-width) {
    margin-top: auto;
  }
}
